@font-face {
  font-family: InterRegular;
  src: url("Inter-Regular.74535f8a.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: InterMedium;
  src: url("Inter-Medium.de3d51e6.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: InterSemiBold;
  src: url("Inter-SemiBold.f13c74ed.ttf");
  font-style: normal;
  font-weight: normal;
}

:root {
  --primary-color: #333;
  --main-bggrey: #efefef;
  --text-light: #999;
  --text-dark: #1e1e1e;
  --aside-bg: #fbfbfb;
  --border: 1px solid #eaeaea;
  --main-blue: #3369f3;
  --light-blue: #e4edfd;
  --main-red: red;
}

body {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  cursor: pointer;
  color: #fff;
  text-align: center;
  background: var(--main-blue);
  border: none;
  border-radius: 8px;
  padding: 12px 0;
  font-family: InterMedium, serif;
  font-size: 13px;
  line-height: 13px;
  display: block;
}

.link {
  width: 100%;
  text-align: center;
  color: var(--main-blue);
  font-family: InterMedium, serif;
  font-size: 11px;
  line-height: 11px;
  text-decoration: none;
  display: block;
}

.form-wrap {
  width: 340px;
  box-sizing: border-box;
  background: #fff;
  border-radius: 12px;
  padding: 50px 30px 30px;
  box-shadow: 0 0 6px #00000024;
}

.title {
  text-align: center;
  font-family: InterMedium, serif;
  font-size: 20px;
  line-height: 20px;
}

.avatar {
  box-sizing: border-box;
  height: 47px;
  width: 47px;
  background-color: var(--main-bggrey);
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.avatar__img {
  width: 100%;
  height: 100%;
}

.avatar .no-photo {
  height: 35%;
  width: 35%;
}

.page-error {
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.error {
  color: var(--text-dark);
  text-align: center;
  font-family: InterMedium;
}

.error__code {
  font-size: 40px;
  line-height: 40px;
}

.error__text {
  margin-top: 20px;
  font-size: 20px;
  line-height: 20px;
}

.error__link {
  margin-top: 70px;
}

.form {
  box-sizing: border-box;
  height: 100%;
  margin: 0;
  padding-top: 24px;
  position: relative;
}

.form .link {
  margin-top: 14px;
}

.form > div {
  position: relative;
}

.form .button {
  width: 100%;
  margin-top: 160px;
}

.form .buttons-wrap {
  width: 100%;
  position: absolute;
  bottom: 30px;
}

.form .error {
  color: var(--main-red);
  margin-top: 7px;
  font-family: InterRegular, serif;
  font-size: 8px;
  line-height: 8px;
  display: none;
}

.form .label {
  z-index: 1;
  color: var(--text-light);
  font-family: InterMedium, serif;
  font-size: 13px;
  line-height: 13px;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 10px;
}

.form .input {
  width: 100%;
  border: none;
  border-bottom: 1px solid var(--main-blue);
  color: var(--text-dark);
  outline: #0000;
  margin-top: 20px;
  padding: 7px 0;
  font-family: InterMedium, serif;
  font-size: 13px;
  line-height: 13px;
  display: block;
}

.form .input:focus ~ label, .form .input.active ~ label {
  font-size: 9px;
  line-height: 9px;
  top: -6px;
}

.form .input.error ~ div {
  display: block;
}

.login, .signin {
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.login .form-wrap {
  height: 450px;
}

.signin .form-wrap {
  height: 615px;
}

.chart {
  display: flex;
}

.chart__aside {
  width: 310px;
  padding: 0 10px;
}

.aside {
  box-sizing: border-box;
  background-color: var(--aside-bg);
  height: 100vh;
  float: left;
  border-right: var(--border);
}

.toprofile {
  text-align: end;
  width: 100%;
  color: var(--text-light);
  margin: 23px 0;
  font-family: InterMedium, serif;
  font-size: 12px;
  line-height: 12px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.toprofile:after {
  content: "";
  width: 5px;
  height: 5px;
  border-top: 1px solid var(--text-light);
  border-right: 1px solid var(--text-light);
  margin-left: 8px;
  display: inline-block;
  transform: rotate(45deg);
}

.search {
  margin-bottom: 14px;
  position: relative;
}

.search__input {
  width: 100%;
  height: 32px;
  box-sizing: border-box;
  background-color: var(--main-bggrey);
  outline: var(--main-bggrey);
  border: none;
  border-radius: 5px;
  padding: 6px 10px 6px 34px;
  font-size: 12px;
  font-weight: 500;
  line-height: 12px;
  display: block;
}

.search__input:focus + div {
  width: 20px;
  left: 10px;
}

.search__input:focus + div span {
  width: 0;
  opacity: 0;
}

.search div {
  height: 20px;
  width: 100%;
  background-color: var(--main-bggrey);
  z-index: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 6px;
}

.search div span {
  color: var(--text-light);
  margin-left: 4px;
  font-family: InterMedium, serif;
  font-size: 12px;
  line-height: 12px;
}

.search:hover div {
  width: 20px;
  left: 10px;
}

.search:hover div span {
  width: 0;
  opacity: 0;
}

.message {
  border-bottom: var(--border);
  cursor: pointer;
  z-index: 1;
  grid-template-columns: 47px 1fr;
  gap: 10px;
  padding: 12px 0;
  display: grid;
  position: relative;
}

.message:first-child {
  border-top: var(--border);
}

.message:after {
  content: "";
  width: 310px;
  height: calc(100% - 4px);
  z-index: -1;
  background-color: #0000;
  transition: all .3s;
  position: absolute;
  top: 2px;
  left: -10px;
}

.message.active:after, .message:hover:after {
  background-color: var(--light-blue);
}

.info {
  grid-template-rows: 13px 30px;
  gap: 4px;
  display: grid;
}

.info__wrap {
  color: var(--text-light);
  grid-template-columns: 1fr 20px;
  gap: 20px;
  display: grid;
}

.info__wrap:first-child {
  justify-content: space-between;
  align-items: end;
  display: flex;
}

.info__date {
  font-family: InterMedium, serif;
  font-size: 9px;
}

.info__text {
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  color: var(--text-light);
  -webkit-box-orient: vertical;
  font-family: InterMedium, serif;
  font-size: 12px;
  line-height: 15px;
  display: -webkit-box;
  overflow: hidden;
}

.info__unread-count {
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  color: #fff;
  background: var(--main-blue);
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-self: end;
  align-items: center;
  padding: 4px;
  font-family: InterMedium, serif;
  font-size: 11px;
  line-height: 12px;
  display: flex;
}

.chart-area {
  box-sizing: border-box;
  height: 100vh;
  width: calc(100% - 310px);
  color: var(--text-light);
  padding: 10px 20px;
  font-family: InterMedium, serif;
  font-size: 12px;
  line-height: 12px;
  position: relative;
}

.chart-area_empty {
  justify-content: center;
  align-items: center;
  display: flex;
}

.friend {
  border-bottom: 1px solid #eaeaea;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  display: flex;
}

.friend__wrap {
  align-items: center;
  display: flex;
}

.friend__name {
  margin-left: 10px;
}

.friend__dottes {
  cursor: pointer;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.friend__dottes:hover {
  background-color: #f5f5f5;
}

.friend__dottes:hover circle {
  fill: var(--main-blue);
}

.name {
  color: #000;
  font-family: InterSemiBold, serif;
  font-size: 13px;
  line-height: 13px;
}

.add-message {
  width: calc(100% - 350px);
  border-top: 1px solid #eaeaea;
  grid-template-columns: 32px 1fr 28px;
  align-items: center;
  gap: 10px;
  padding: 10px 0;
  display: grid;
  position: fixed;
  bottom: 0;
}

.add-message__input {
  background: #efefef;
  border: none;
  border-radius: 30px;
  outline: #0000;
  padding: 10px 20px;
}

.add-message .clip {
  cursor: pointer;
}

.add-message .clip path {
  transition: all .3s;
}

.add-message .clip:hover path {
  fill: var(--main-blue);
}

.profile {
  color: var(--text-dark);
  font-family: InterMedium, serif;
  font-size: 13px;
  line-height: 13px;
}

.profile__aside {
  justify-content: center;
  align-items: center;
  padding: 0 18px;
  display: flex;
}

.profile__info {
  height: 100vh;
  width: 510px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.profile__avatar {
  width: 130px;
  height: 130px;
  cursor: pointer;
  background: #efefef;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.profile__avatar:hover:after {
  content: "Поменять аватар";
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  color: #fff;
  text-align: center;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 13px;
  display: flex;
  position: absolute;
}

.profile__link {
  text-align: start;
  border-bottom: var(--border);
  padding: 10px 0;
}

.profile__exit {
  color: var(--main-red);
  text-align: start;
  cursor: pointer;
  background: none;
  border: none;
  padding: 10px 0;
}

.profile__form {
  width: 100%;
  padding: 100px 0 60px;
  position: relative;
}

.profile__form .input-wrap {
  border-bottom: var(--border);
  justify-content: end;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

.profile__form .input {
  text-align: end;
  border: none;
  outline: #0000;
}

.profile__form .label {
  position: absolute;
  left: 0;
}

.profile__form :last-child {
  border-bottom: none;
}





